<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img src="../assets/meeting.png" alt />
        <span>AX-MEET | </span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!--  -->
        <el-menu :default-active="activePath" :router="true" :collapse-transition="false" :collapse="isCollapse" :unique-opened="true" background-color="#5866e6" text-color="#fff" active-text-color="#409eff">
          <!--  -->
          <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
            <!--  -->
            <template slot="title">
              <!--  -->
              <i :class="iconsObj[item.id]"></i>
              <!--  -->
              <span>{{ item.authName }}</span>
            </template>
            <!--  -->
            <el-menu-item @click="saveNavState('/' + subItem.path)" :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id">
                <template slot="title">
                    <!--  -->
                    <i :class="iconsObj[subItem.id]"></i>
                    <!--  -->
                    <span>{{ subItem.authName }}</span>
                </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
    data () {
        return {
            // 
            menulist: [{
                id: 101,
                authName: '',
                path: 'null',
                children: [
                    { id: 1011, authName: '', path: 'book' },
                    { id: 1012, authName: '', path: 'cancelBook' }
                ]
                },
                {
                id: 102,
                authName: '/',
                path: 'null',
                children: [
                    { id: 1021, authName: '', path: 'approved' },
                    { id: 1022, authName: '', path: 'notApproved' },
                    { id: 1023, authName: '', path: 'approving' },
                    { id: 1024, authName: '', path: 'history' }
                ]
                }],
            iconsObj: {
                101: 'iconfont icon-baobiao',
                102: 'iconfont icon-users',
                1011: 'el-icon-circle-plus',
                1012: 'el-icon-error',
                1021: 'el-icon-folder-checked',
                1022: 'el-icon-folder-opened',
                1023: 'el-icon-loading',
                1024: 'el-icon-document-copy'
            },
            isCollapse: false,
            activePath: ''
        }
    },
    created () {
        this.activePath = window.sessionStorage.getItem('activePath')
    },
    methods: {
        logout () {
            window.sessionStorage.clear()
            this.$router.push('/login')
        },
        // ,
        toggleCollapse () {
            this.isCollapse = !this.isCollapse
        },
        // 
        saveNavState (activePath) {
          window.sessionStorage.setItem('activePath', activePath)
          this.activePath = activePath
        }
    }
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}

.el-header {
  background-color: #d1cafc;
  display: flex;
  justify-content: space-between;
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  align-items: center;
  color: #333;
  font-size: 27px;
  > div {
    display: flex;
    align-items: center;
    height: 100%;
    img {
      height: 100%;
    }
    span {
      margin-left: 15px;
    }
  }
  backdrop-filter: blur(10px);
  background-color: #FFFFFFFF;
}

.el-menu {
    border-right: 0;
}

.el-aside {
  background-color: #5866e6;

  .toggle-button {
      background-color: #7a8cff;
      cursor: pointer;
      color: #fff;
      font-size: 10px;
      line-height: 24px;
      text-align: center;
      letter-spacing: .2em;
  }

  .el-main {
      border-right: 0;
  }
}

.iconfont {
    margin-right: 10px;
}
</style>
