<template>
    <div class="login_container">
        <div class="login_box">
            <!--  -->
            <div class="avatar_box">
                <img src="../assets/user.png" alt="">
            </div>

            <!--  -->
            <el-form ref="loginFormRef" class="login_form" :rules="loginFormRules" :model="loginForm">
                <!--  -->
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user" placeholder="" ></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item prop="password">
                    <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password" placeholder=""></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item class="btns">
                    <el-button type="primary" @click="login"></el-button>
                    <el-button type="info" @click="resetLoginForm"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            // 
            loginForm: {
                username: '',
                password: ''
            },
            // 
            loginFormRules: {
                // 
                username: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
                ],
                // 
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 10, message: ' 5  10 ', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        // 
        resetLoginForm () {
            this.$refs.loginFormRef.resetFields()
        },
        login () {
            this.$refs.loginFormRef.validate(async valid => {
                if (!valid) return
                const { data: res } = await this.$http.post('/login', this.loginForm)
                if (res.code !== 100) return this.$message.error('')
                this.$message.success('')
                // 1.tokensessionStorage
                //   1.1 API
                //   1.2 tokentokensessionStorage
                window.sessionStorage.setItem('token', res.extend.token)
                // 2./home
                this.$router.push('/home')
            })
        }
    }
}
</script>

<style lang="less" scoped>
.login_container{
  background-image: url("https://images.pexels.com/photos/19492462/pexels-photo-19492462.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  height: 100%;
  backdrop-filter: blur(10px);
}

.login_box{
    width: 600px;
    height: 300px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    backdrop-filter: blur(10px);

    .avatar_box{
        height: 130px;
        width: 130px;
        background-color: rgba(255, 255, 255, 0.7);
        border: 1px solid rgba(238, 238, 238, 0.7);
        border-radius: 50%;
        padding: 10px;
        position: absolute;
        transform: translate(-50%, -50%);
        left: 50%;
        img{
            width: 100%;
            height: 100%;
            background-color: #eee;
            border-radius: 50%;
        }
    }
}

.login_form{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}

.btns{
    display: flex;
    justify-content: flex-end;
}
</style>
